import React from 'react'
import axios from 'axios'

const Home = () => {
  const handleRequest = (event) => {
    event.preventDefault();
    const {something,link} = event.target.elements;
    const data = JSON.parse(something.value);
    axios.post(link, data).then(r=>console.log(r))
  }
  return (
    <div className="App"> 
<div class="container pt-5">
  <div class="card">
    <div class="card-body">
      <div class="container mb-3">
         <h5 class="card-title float-left">HTTP Testing</h5>
         <div class="dropdown text-right">
          <button class="btn btn-secondary dropdown-toggle" type="button" data-target="#myDropdown" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            Method
          </button>
          <div class="dropdown-menu" aria-labelledby="dropdownMenuButton"> {/* Ini untuk button dropdown yang get itu */}
            <a class="dropdown-item nav-link" >Post</a> {/* Jumlahnya berapa aku gak tau tapi */}
            <a class="dropdown-item nav-link">Get</a>
          </div>
        </div>
      </div>
    {/* Akhir dari sub judul dan dropdown get */}


      {/* Tempat untuk naruh output mulai dari sini */}
      {/* Ada dua kotak, satu agak besar, satunya kecil, bisa dihapus kalau ngerasa kebanyakan */}
      <div class="container pt-1 pb-1 pr-1 pl-3 mb-3 bg-light">
        <h6 class="text-black text-left">URLNYA Lorem ipsum dolor sit amet consectetur.</h6> {/* Ini tempat untuk naruh output */}
      </div>
      <div class="container pt-1 pb-5 pr-1 pl-3 mb-3 bg-light">
        <h6 class="text-black text-left">Hasil</h6> {/* Ini juga untuk output ya */}
        <pre>
          {JSON.stringify({abc:'sdfasdfsa'},null,2)}
        </pre>
      </div>
      <div class="container bg-light pr-5 pl-5 pb-2 pt-2">
      <form onSubmit={handleRequest}>

      <div class="form-group text-left">
        <label for="exampleFormControlSelect1">Media Type</label>
        <select class="form-control" id="exampleFormControlSelect1">
          <option>application/json</option> {/* Ini yang media type, ada berapa aku kurang tau, jadinya aku isiin 5 */}
          <option>form</option>
        </select>
      </div> 
      <div className="form-group">
                        <label htmlFor="exampleInputPassword1">Link</label>
                        <input name="link" className="form-control" id="exampleInputPassword1" placeholder="link"/>
                  </div>
          <div class="form-group text-left">
            <label for="exampleFormControlTextarea1">Content</label>
            <textarea name="something" class="form-control" id="exampleFormControlTextarea1" rows="3"></textarea> {/* Ini untuk naruh teks yang mau diinput */}
          </div>
          <button type="submit" class="btn btn-primary btn-lg float-right mt-3">Submit</button> {/* Ini tombol submitnya ya */}  
      </form>

      </div>
    </div>
  </div>
</div>
    </div>
  );
}

export default Home;


